.rootCont {
    /* height: 90vh; */
    width: 70%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-top: 10vh;
    padding-bottom: 10vh;
    text-align: center;
  }
  
  .rootCont h1 {
    color: #383874;
  
    margin: 0 auto;
  }
  
  .headingH1 {
    text-align: center;
    margin-top: 40px;
    font-weight: 800;
    font-size: 35px;
    /* color: blueviolet; */
    color: blueviolet;
    letter-spacing: 0.2rem;
  }
  .skillsContFront {
    display: grid;
    grid-template-columns: repeat(6, 16.6%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 50px;
    /* margin-left: -50px; */
    /* height: 50vh; */
  }
  .skillsContBack {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 50px;
    /* height: 50vh; */
  }
  .toolsDiv {
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 50px;
    /* height: 50vh; */
  }
  .skillCard {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100px;
    margin: auto;
  }
  .skillIcon {
    font-size: 60px;
    color: blueviolet;
  }
  .expSkillIcon {
    font-size: 31px;
    font-weight: 470;
    color: rgb(208,135,104);
  }
  
  .reactIcon:hover {
    color: rgb(94, 211, 243);
  }
  .reduxIcon:hover {
    color: rgb(118, 76, 183);
  }
  .jsIcon:hover {
    color: rgb(247, 223, 30);
  }
  .muiIcon:hover {
    color: rgb(0, 129, 203);
  }
  .htmlIcon:hover {
    color: rgb(233, 98, 40);
  }
  .cssIcon:hover {
    color: rgb(37, 75, 221);
  }
  .nodeIcon:hover {
    color: rgb(95, 151, 89);
  }
  .mongoIcon:hover {
    color: rgb(82, 167, 75);
  }
  .gitIcon:hover {
    color: rgb(232, 78, 49);
  }
  .expIcon {
    margin: 0;
  }
  .expIcon:hover {
    color: black;
  }
  @media only screen and (max-width: 1024px) {
    .skillIcon {
      font-size: 30px;
      /* color: rgb(208,135,104); */
    }
    .rootCont {
      width: 90%;
    }
    .skillsContFront {
      grid-template-columns: repeat(3, 33.3%);
      /* height: 120vh; */
    }
  }
  